import { useState, useMemo } from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import { useTasks } from './store/Store'
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button';
import { Grid } from "@material-ui/core";
import { Box } from '@material-ui/core';

function TaskList() {
    const [filter, setFilter] = useState("all");
    const { tasks, toggleTask, removeTask } = useTasks();
    const filteredTasks = useMemo(() => {
        
        if (filter === "all") {
            return tasks;
        }else if (filter === "completed") {
            return tasks.filter(t => t.completed);
        }else if (filter === "notCompleted") {
            return tasks.filter(t => !t.completed);
        }

    }, [tasks, filter]);

    return (
        <>
            <List>
                {filteredTasks.map((task) => {
            
                    return (
                    <ListItem key={task.id}>
                            <ListItemText primary={task.text} />
                            <ListItemSecondaryAction>
                                <Checkbox checked={task.completed} onClick={() => toggleTask(task.id) }/>
                                <IconButton onClick={() => removeTask(task.id)} aria-label="delete">
                                    <DeleteIcon />
                                </IconButton>
                            </ListItemSecondaryAction>
                    </ListItem>
                    );
                })}
            </List>
            <Grid container justify= "center">
                <Box p={1}>
                    <Button variant="contained" color="secondary" onClick={() => setFilter("all")}>All</Button>
                </Box>
                <Box p={1}>
                    <Button variant="contained" color="secondary" onClick={() => setFilter("completed")}>Completed</Button>
                </Box>
                <Box p={1}>
                    <Button variant="contained" color="secondary" onClick={() => setFilter("notCompleted")}>Due</Button>
                </Box>
            </Grid>
        </>
      );
    };

export default TaskList
